import React from 'react' ;
import { Feature } from '../../components';
import './features.css' ;
import g1 from "../../assets/g1.png"
import g2 from "../../assets/g2.png"
import g3 from "../../assets/g3.png"

const featuresData = [

  {
    title: 'Find Winning Products in less than 2 minutes',
    text: '',
  },
  {
    title: ' Check which Products are getting Eyeballs and Attention',
    text: '',
  },
 
  {
    title: ' Discover the Rising Stars in your Niche',
    text: ''
    ,
  },
  {
    title: ' Analyze any listing to see its performance & insider hidden details',
    text: ''
  },
  {
    title: ' Find the Conversion Rate of the Products',
    text: ''
  }
]



const Features = () => {
  return (
    <div className="gpt3__features section__padding" id="features">
    <div className="gpt3__features-heading">
      <h1 className="gradient__text main__heading">Simple way to Unlock Etsy Success in Minutes without  
<div className='h_div button'>  Trial and Error
        </div>
</h1>
      <p>What We Offer</p>
    </div>
    <div className='gpt3__features-container'>
      {
        featuresData.map((item,index)=>(
          <Feature title={" " + item.title} text={item.text} key={item.title + index}/>
        ))
      }
    </div>

    <div className='images_buy'>
      <img src={g1} alt="" />
      <img src={g2} alt="" />
      <img src={g3} alt="" />
    </div>



    {/* <div className='gpt3__footer-btnn gradient-border' id="box">
        <p> Use Forever &nbsp; &nbsp; &nbsp; &nbsp;&nbsp;  Works on Cloud &nbsp; &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; Unlocks Growth</p>
       
      </div> */}


    </div>
  )
}

export default Features;
